// __tests__/integration/full-user-journey.test.js
import React from 'react';
import { render, fireEvent, waitFor, within } from '@testing-library/react-native';
import { router } from 'expo-router';

// √âcrans
import Dashboard from '../../src/screens/Dashboard';
import LevelSelection from '../../src/screens/LevelSelection';
import ExerciseSelection from '../../src/screens/ExerciseSelection';
import VocabularyExercise from '../../src/screens/exercises/vocabulary';

// Mocks
jest.mock('expo-router', () => ({
  useFocusEffect: jest.fn(callback => callback()),
  router: {
    push: jest.fn(),
    back: jest.fn(),
  },
  Stack: ({ children }) => <>{children}</>,
  useLocalSearchParams: () => ({}),
  useNavigation: () => ({
    navigate: jest.fn(),
    goBack: jest.fn(),
    canGoBack: jest.fn(() => true),
    addListener: jest.fn(),
    isFocused: jest.fn(() => true),
  }),
}));

// Mock des hooks de donn√©es et de contexte
const mockUpdateProgress = jest.fn();
let mockProgressData = {};

jest.mock('../../src/hooks/useRealTimeProgress', () => () => ({
  getLevelProgress: (level) => mockProgressData[level]?.levelProgress || 0,
  getExerciseProgress: (exercise, level) => mockProgressData[level]?.[exercise] || 0,
  hasProgress: (exercise, level) => (mockProgressData[level]?.[exercise] || 0) > 0,
  refresh: jest.fn(),
}));

jest.mock('../../src/contexts/ProgressContext', () => ({
  ...jest.requireActual('../../src/contexts/ProgressContext'),
  useProgress: () => ({
    updateProgress: mockUpdateProgress,
    progress: { overall: 0, levels: mockProgressData },
    isLoading: false,
  }),
}));

// Mock du hook de vocabulaire pour simplifier l'exercice
const mockUseVocabulary = jest.fn();
jest.mock('../../src/screens/exercises/vocabulary/hooks/useVocabulary', () => mockUseVocabulary);

// Mock des autres hooks n√©cessaires
jest.mock('../../src/screens/Dashboard/hooks/useDashboardState', () => ({
  useDashboardState: jest.fn(() => ({
    showLevelProgress: false,
    setShowLevelProgress: jest.fn(),
    openLevelProgressModal: jest.fn(),
    closeLevelProgressModal: jest.fn(),
    refreshing: false,
    activeTab: "home",
    setActiveTab: jest.fn(),
    onRefresh: jest.fn().mockResolvedValue(),
  })),
}));

jest.mock('../../src/hooks/useLastActivity', () => () => ({
  lastActivity: null,
  isLoading: false,
  saveActivity: jest.fn(),
  clearActivity: jest.fn(),
  reload: jest.fn(),
}));

jest.mock('../../src/hooks/useActivityMetrics', () => () => ({
  currentStreak: 5,
  wordsToday: 0,
  timeToday: 0,
  isLoading: false,
}));

describe('Parcours utilisateur complet - Navigation et progression', () => {
  beforeEach(() => {
    jest.clearAllMocks();
    mockProgressData = {}; // Pas de progression au d√©but
    
    // Configuration par d√©faut du mock useVocabulary
    mockUseVocabulary.mockReturnValue({
      loaded: true,
      currentWord: { word: 'Hello', translation: 'Bonjour', example: 'Hello, how are you?' },
      wordIndex: 0,
      display: {
        wordCounter: '1 / 15',
        categories: ['Basics'],
      },
      isLastWordInExercise: false,
      handleNext: jest.fn().mockReturnValue({ completed: false }),
      saveData: jest.fn().mockResolvedValue(undefined),
      toggleTranslation: jest.fn(),
    });
  });

  describe('Sc√©nario 1: Parcours complet Dashboard ‚Üí Level ‚Üí Exercise ‚Üí Vocabulary', () => {
    test('Navigation compl√®te entre tous les √©crans avec progression', async () => {
      // Phase 1: Dashboard initial
      const { getByTestId, getByText, rerender } = render(<Dashboard />);
      
      // V√©rifier l'√©tat initial du dashboard
      expect(getByText('üìä Aujourd\'hui')).toBeTruthy();
      expect(getByText('‚ö° Actions rapides')).toBeTruthy();
      expect(getByText('üèÜ Progression g√©n√©rale')).toBeTruthy();
      
      // Phase 2: Navigation vers Level Selection
      const levelsButton = getByTestId('levels-tab');
      fireEvent.press(levelsButton);
      
      // Rerender avec LevelSelection
      rerender(<LevelSelection />);
      
      await waitFor(() => {
        expect(getByText('Choisissez votre niveau')).toBeTruthy();
        expect(getByTestId('level-1')).toBeTruthy();
      });
      
      // Phase 3: S√©lection du niveau 1
      const level1Card = getByTestId('level-1');
      expect(within(level1Card).getByText('0%')).toBeTruthy();
      
      fireEvent.press(level1Card);
      
      // Phase 4: Navigation vers Exercise Selection
      rerender(<ExerciseSelection />);
      
      await waitFor(() => {
        expect(getByText('Choisissez votre exercice')).toBeTruthy();
        expect(getByTestId('vocabulary-button')).toBeTruthy();
      });
      
      // Phase 5: Lancement de l'exercice de vocabulaire
      const vocabularyButton = getByTestId('vocabulary-button');
      fireEvent.press(vocabularyButton);
      
      // Phase 6: Exercice de vocabulaire
      rerender(<VocabularyExercise route={{ params: { level: '1', mode: 'classic' } }} />);
      
      await waitFor(() => {
        expect(getByText('Hello')).toBeTruthy();
        expect(getByText('1 / 15')).toBeTruthy();
      });
      
      // Phase 7: Progression dans l'exercice
      const nextButton = getByText('Suivant');
      fireEvent.press(nextButton);
      
      // V√©rifier que la progression a √©t√© sauvegard√©e
      expect(mockUpdateProgress).toHaveBeenCalledWith('1', 'vocabulary', expect.any(Number));
      
      // Phase 8: Retour et v√©rification des mises √† jour
      // Simuler la mise √† jour de la progression
      mockProgressData['1'] = {
        levelProgress: 7, // 1 mot sur 15 = ~7%
        vocabulary: 7,
      };
      
      // Retour √† Exercise Selection
      rerender(<ExerciseSelection />);
      
      await waitFor(() => {
        expect(getByText('7%')).toBeTruthy(); // Progression mise √† jour
      });
      
      // Retour √† Level Selection
      rerender(<LevelSelection />);
      
      await waitFor(() => {
        const updatedLevel1Card = getByTestId('level-1');
        expect(within(updatedLevel1Card).getByText('7%')).toBeTruthy();
      });
      
      // Retour au Dashboard
      rerender(<Dashboard />);
      
      await waitFor(() => {
        expect(getByText('üìä Aujourd\'hui')).toBeTruthy();
        // Les m√©triques devraient √™tre mises √† jour
        expect(getByText('1')).toBeTruthy(); // 1 mot aujourd'hui
      });
    });
  });

  describe('Sc√©nario 2: Gestion des erreurs et cas limites', () => {
    test('Gestion de la navigation avec donn√©es manquantes', async () => {
      // Test avec des donn√©es de progression corrompues
      mockProgressData['1'] = {
        levelProgress: null,
        vocabulary: undefined,
      };
      
      const { getByTestId, getByText } = render(<LevelSelection />);
      
      await waitFor(() => {
        const level1Card = getByTestId('level-1');
        // Devrait afficher 0% par d√©faut
        expect(within(level1Card).getByText('0%')).toBeTruthy();
      });
    });

    test('Navigation avec exercice en cours', async () => {
      // Simuler un exercice en cours
      mockProgressData['1'] = {
        levelProgress: 45,
        vocabulary: 45,
      };
      
      const { getByTestId, getByText } = render(<LevelSelection />);
      
      await waitFor(() => {
        const level1Card = getByTestId('level-1');
        expect(within(level1Card).getByText('45%')).toBeTruthy();
        // Le bouton devrait afficher "Continuer"
        expect(within(level1Card).getByText('Continuer')).toBeTruthy();
      });
    });
  });

  describe('Sc√©nario 3: Coh√©rence des donn√©es entre √©crans', () => {
    test('Synchronisation des m√©triques entre Dashboard et Level Selection', async () => {
      // Phase 1: Dashboard avec progression
      mockProgressData['1'] = { levelProgress: 25, vocabulary: 25 };
      
      const { getByText, rerender } = render(<Dashboard />);
      
      await waitFor(() => {
        expect(getByText('üèÜ Progression g√©n√©rale')).toBeTruthy();
      });
      
      // Phase 2: V√©rifier que Level Selection affiche la m√™me progression
      rerender(<LevelSelection />);
      
      await waitFor(() => {
        expect(getByText('25%')).toBeTruthy();
      });
      
      // Phase 3: Mise √† jour de la progression
      mockProgressData['1'] = { levelProgress: 50, vocabulary: 50 };
      
      // Retour au Dashboard
      rerender(<Dashboard />);
      
      await waitFor(() => {
        // Les m√©triques devraient √™tre coh√©rentes
        expect(getByText('üèÜ Progression g√©n√©rale')).toBeTruthy();
      });
    });
  });
});
